.button {
  padding: 4px 12px;
  border-radius: 8px;
  border: 1px solid gray;
  background-color: white;
  color: black;
  outline: none;
}

.button:hover {
  background-color: #f0f0f0;
}

.button:active {
  color: gray;
}

.button_type_circle {
  border-radius: 50%;
  padding: 4px;
}
